import React, { PureComponent } from 'react';

const projects = [
  {
    title: 'Meteor Migration System',
    where: 'Opensource Plugin',
    description: 'A simple migration system for Meteor supporting string version migrations and command line usage.',
    link: 'https://atmospherejs.com/patelutsav/meteor-migrations',
    github: 'https://github.com/patelutsav/meteor-migrations',
  },
  {
    title: 'Trackr System: Real-Time System for Tracking Nurse Rounding',
    where: 'Graduate Project',
    description: 'This project presents a real-time automated system for monitoring the hourly rounds performed by the nurses. The developed system, named "Trackr", combines various technologies to determine whether a patient has been visited during a round. The Trackr system consists of three components: the BLE (Bluetooth Low Energy) device, the Nursing Home mobile application for Android, and the Trackr web application. The functionality of Trackr system requires that BLE tags are attached to the patients and that nurses carry Android phones with the Nursing Home mobile application. The mobile application scans the nearest patient\'s BLE tag signal and records his/her visit in the Trackr web application. The web application provides an interface through which nursing staff can monitor the hourly rounds.',
    demo: 'http://web.cs.dal.ca/~upatel/nh',
  },
  {
    title: 'Marriage Registration System',
    where: 'Internship Project',
    description: 'This was my Internship Project at Microtech IT Systems Pvt. Ltd. in India. In this project, I have developed an online Marriage Registration System in which users can create their profile and register their marriage online. This was two-step process in which the user first registers their marriage details online and then they book an appointment to the nearest marriage registrar office to verify the document and collect their marriage certificate. In this project, I have developed two modules 1) Client Side Module and 2) Registrar Office Module. The implementation of the project was done in MVC architecture.',
  },
  {
    title: 'Form Builder',
    where: 'FreeLancer Project',
    description: 'This is an AngularJS project build for developing dynamic forms using different html form tags. By using pre- existing open source code for drag and drop feature in HTML5 and AngularJS, the project provides an interface to build a form on webpage itself which can be saved to use it later for various purpose. The project also provides a feature to modify an existing form to make changes that can be useful to the user. Users can also save as the existing form if they are using same form components to build another form.',
    demo: 'http://web.cs.dal.ca/~upatel/formbuilder',
  },
];

export class Projects extends PureComponent {
  getProjects() {
    return projects.map(({
      title, where, description, demo, github, link
    }) => (
      <div className="flex flex-column project" key={title}>
        <div className="title">{title}</div>
        <div className="where">{where}</div>
        <div className="description">{description}</div>
        <div className="flex flex-row links">
          {demo && <a href={demo} target="_blank" rel="noopener noreferrer">Demo</a>}
          {link && <a href={link} target="_blank" rel="noopener noreferrer">Link</a>}
          {github && <a href={github} target="_blank" rel="noopener noreferrer">Github</a>}
        </div>
      </div>
    ));
  }

  render() {
    return (
      <div className="flex flex-column center margin-100">
        <div>
          <h1>Projects</h1>
          <hr className="light-backgound" />
        </div>
        {this.getProjects()}
      </div>
    );
  }
}
