import React, { PureComponent } from 'react';
import { Chip } from '../../common/components';

export class Education extends PureComponent {
  getEducationSection() {
    const data = [
      {
        degree: 'Master of Applied Computer Science',
        name: 'Dalhousie University, Halifax, Canada',
        year: '2016',
        skills: [
          'Machine Learning for Big Data',
          'Data Mining and Data Warehousing',
          'Managing Data in Distributed System',
          'Advanced Topics in Natural Language Processing',
          'Advanced Topics of Computer Networks',
          'Ubiquitous Computing',
        ],
      },
      {
        degree: 'Bechlor of Computer Engineering',
        name: 'U. V. Patel College of Engineering, India',
        year: '2014',
        skills: [
          'Object Oriented Programming',
          'Design and Analysis of Algorithms',
          'Distributed Systems',
          'Theory of Computation',
          'Database Management System',
          'Application Development Tools',
          'Operating System',
        ],
      },
    ];
    return data.map(({
      degree, name, year, skills
    }, index) => (
      <div className={index === 0 ? 'education' : 'education-right'} key={degree}>
        <div className="degree">
          {degree}
          {' '}
            |
          {' '}
          {year}
        </div>
        <div className="name">{name}</div>
        <div className="padding-10-0">
          {skills.map(skill => <Chip variant="outlined" label={skill} color="primary" key={skill} className={index === 0 ? 'dark-hover margin-right-bottom-5' : 'dark-hover margin-left-bottom-5'} />)}
        </div>
      </div>
    ));
  }

  render() {
    return (
      <div className="flex flex-column center margin-100">
        <div>
          <h1>Education</h1>
          <hr className="light-backgound" />
        </div>
        <div className="flex flex-row education-container">
          {this.getEducationSection()}
        </div>
      </div>
    );
  }
}
